<template>
  <div class="col-12 grid-view-container">
    <div class="table-responsive">
      <table class="table table-sm table-hover table-bordered">
        <thead class="bg-primary">
          <tr role="row">
            <th v-for="(header, key) in griddata.Headers" v-bind:key="key">{{ header.Title }}</th>
            <th v-if="griddata.CommandButtons !== null && griddata.CommandButtons.length > 0" class="col-3"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="record in griddata.Records" v-bind:key="record[0]">
            <td v-for="(cell, key) in record" v-bind:key="key" v-if="key > 1 && griddata.Headers[0].IsClickable === false">{{cell}}</td>
            <td v-for="(cell, key) in record" v-bind:key="key" v-if="key > 1 && griddata.Headers[0].IsClickable === true">
              <b-link href="javascript:void(0)" @click="onRowClick(record[0])">{{cell}}</b-link>
            </td>
            <td 
              v-if="griddata.CommandButtons !== null && griddata.CommandButtons.length > 0"
              class="p-0 pl-2"
            >
              <div class="btn-toolbar">
                <div class="btn-group-sm">
                  <a
                    v-for="(comm, key) in griddata.CommandButtons"
                    v-bind:key="key"
                    v-if="comm.Type === 'Edit'"
                    title="Edit"
                    href="javascript:void(0)"
                    class="btn btn-outline-primary"
                    @click="onEditClick(record[0])"
                  >
                    <i class="fa fa-pencil fa-fw fa-lg"></i>
                  </a>
                  <a
                    v-for="(comm, key) in griddata.CommandButtons"
                    v-bind:key="key"
                    v-if="comm.Type === 'Delete'"
                    title="Delete"
                    href="javascript:void(0)"
                    class="btn btn-outline-danger"
                  >
                    <i class="fa fa-trash fa-fw fa-lg"></i>
                  </a>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <nav v-if="griddata.Pagination !== null">
      <ul class="pagination pagination-sm justify-content-end">
        <!-- Download as excel -->
        <li class="page-item mr-3" v-if="griddata.Pagination.IsExcelDownload === true">
          <a class="page-link text-success btn-excel-download" href="javascript:void(0)">
            <i class="fa fa-file-excel-o fa-fw fa-lg"></i> Download as excel
          </a>
        </li>

        <template v-if="griddata.Pagination.CurrentPage === 1">
          <!-- First (disabled)-->
          <li class="page-item disabled">
            <a class="page-link" href="javascript:void(0)">First</a>
          </li>

          <!-- Previous (disabled)-->
          <li class="page-item disabled">
            <a class="page-link" href="javascript:void(0)" aria-label="Previous">
              <i class="fa fa-angle-double-left fa-fw"></i>
            </a>
          </li>
        </template>

        <template v-if="griddata.Pagination.CurrentPage !== 1">
          <!-- First -->
          <li class="page-item">
            <a class="page-link page-link-grid-view" data-pageno="1" href="javascript:void(0)">First</a>
          </li>

          <!-- Previous -->
          <li class="page-item">
            <a
              class="page-link page-link-grid-view"
              :data-pageno="griddata.Pagination.CurrentPage-1"
              href="javascript:void(0)"
              aria-label="Previous"
            >
              <i class="fa fa-angle-double-left fa-fw"></i>
            </a>
          </li>
        </template>

        <template v-for="(page, key) in griddata.Pagination.Pages">
          <li
            class="page-item active"
            v-if="griddata.Pagination.CurrentPage === page"
            v-bind:key="key"
          >
            <a
              class="page-link page-link-grid-view"
              :data-pageno="page"
              href="javascript:void(0)"
            >{{page}}</a>
          </li>
          <li class="page-item" v-if="griddata.Pagination.CurrentPage !== page" v-bind:key="key">
            <a
              class="page-link page-link-grid-view"
              :data-pageno="page"
              href="javascript:void(0)"
            >{{page}}</a>
          </li>
        </template>

        <template v-if="griddata.Pagination.CurrentPage === griddata.Pagination.PageCount">
          <!-- Next (disabled) -->
          <li class="page-item disabled">
            <a class="page-link" href="javascript:void(0)" aria-label="Last">
              <i class="fa fa-angle-double-right fa-fw"></i>
            </a>
          </li>

          <!-- Last (disabled) -->
          <li class="page-item disabled">
            <a class="page-link" href="javascript:void(0)">Last</a>
          </li>
        </template>

        <template v-if="griddata.Pagination.CurrentPage !== griddata.Pagination.PageCount">
          <!-- Next -->
          <li class="page-item">
            <a
              class="page-link page-link-grid-view"
              :data-pageno="griddata.Pagination.CurrentPage+1"
              href="javascript:void(0)"
              aria-label="Last"
            >
              <i class="fa fa-angle-double-right fa-fw"></i>
            </a>
          </li>

          <!-- Last -->
          <li class="page-item">
            <a
              class="page-link page-link-grid-view"
              :data-pageno="griddata.Pagination.PageCount"
              href="javascript:void(0)"
            >Last</a>
          </li>
        </template>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  name: "tablegrid",
  props: ["griddata"],
  methods: {
    onEditClick(id) {
      this.$router.push('edit/' + id)
    }
  }
};
</script>
